@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
main, ul{
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: rgb(0, 0, 0);
  /* background: linear-gradient(54deg, rgb(255, 255, 255) 0%, rgb(0, 0, 0) 35%, rgb(252, 56, 56) 100%); */
  padding: 10px;
}
.App{
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  border: .1px rgb(250, 243, 243);
}
.divSep, form{
  -webkit-flex: 1 1;
          flex: 1 1;
  border-radius: 10px;
}
.divSep{
  display: -webkit-flex;
  display: flex;
  /* background-image: url("https://images.unsplash.com/photo-1488415032361-b7e238421f1b?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1949&q=80"); */
  background-size: cover;
  box-shadow: inset 0px 0px 15px 2px rgb(94, 89, 89);
  border: .51px solid white;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
form{
  background:rgb(224, 217, 217);
  border: .51px solid rgb(255, 255, 255);
  background: linear-gradient(54deg, rgb(0, 0, 0) 0%, rgb(88, 83, 92) 35%, rgb(40, 41, 48) 100%);
}
.p{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}
label, input{
  margin-top: 10px;
  color: rgb(0, 0, 0);
  font-family: 'DotGothic16', sans-serif;
}
input{
  border: .5px inset black;
}
.btn{
  border: .5px solid rgb(0, 0, 0);
  border-radius: 2px;
}
.locationbox{
  display: -webkit-flex;
  display: flex;
  list-style-type: disc;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  margin-top: 30vh;
  font-family: 'DotGothic16', bold, sans-serif;
  font-size: 40px;
  
}
.initPage{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: lemonchiffon;
  padding: 30px;
  text-align: center;
  -webkit-flex-direction: center;
          flex-direction: center;
  box-shadow: inset 1px 1px 10px 1px black ;
  border: inset 2px rgb(255, 230, 230);
  font-family: 'DotGothic16', bold, sans-serif;
  font-size: 10px;
}
li{
  list-style: square;
  -webkit-justify-content: start;
          justify-content: start;
  text-align: start;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  font-size: 20px;
  color: rgb(0, 0, 0);
  /* -webkit-text-stroke: .7px rgb(255, 255, 255); */
}
.locationbox{
  font-size: 40px;
}
p{
  display: -webkit-flex;
  display: flex;
  color: antiquewhite;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100px;
  font-family: 'DotGothic16', sans-serif;
  font-size: 79px;
  width: 200px;
  border: 1px solid rgb(0, 0, 0);
  border-radius: 10px;
  background: rgb(10, 9, 9);
  box-shadow: 1px 1px 10px 1px white;
  -webkit-text-stroke: 1px limegreen;
}
h1{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 18px;
  color: black;
  max-width: 100vh;
}


